import { createMaterialTopTabNavigator } from "@react-navigation/material-top-tabs"
import { NavigationContainer } from "@react-navigation/native"

const Tab = createMaterialTopTabNavigator()

const Null = () => <></>

export default function TopBar() {
    return (
        <div style={{ width: "100vw" }}>
            <NavigationContainer>
                <Tab.Navigator
                    tabBarOptions={{
                        indicatorStyle: {
                            backgroundColor: null,
                        },
                    }}
                >
                    <Tab.Screen
                        name="Home"
                        component={Null}
                        listeners={{
                            tabPress: (e) =>
                                console.log("Home press", e.target),
                        }}
                    />
                    <Tab.Screen
                        name="Settings"
                        component={Null}
                        listeners={{
                            tabPress: (e) => console.log("Settings", e.target),
                        }}
                    />
                </Tab.Navigator>
            </NavigationContainer>
        </div>
    )
}
